    </div>

    <hr id="formData" />
    <div class="col-md-12">
      <v-card>
        <v-card-title>
          Fill in Form Data
        </v-card-title>
        <v-card-text>
          <validation-observer ref="observer" v-slot="{ handleSubmit }">
            <b-form @submit.stop.prevent="handleSubmit(onSubmit)">
              <validation-provider
                name="Name"
                rules="required|min:5"
                v-slot="validationContext"
              >
                <b-form-group
                  id="example-input-group-1"
                  label="Sector Title"
                  label-for="example-input-1"
                >
                  <b-form-input
                    id="example-input-1"
                    name="example-input-1"
                    v-model="form.title"
                    :state="getValidationState(validationContext)"
                    aria-describedby="input-1-live-feedback"
                  ></b-form-input>

                  <b-form-invalid-feedback id="input-1-live-feedback">{{
                    validationContext.errors[0]
                  }}</b-form-invalid-feedback>
                </b-form-group>
              </validation-provider>

              <validation-provider
                name="information"
                rules="required|min:5"
                v-slot="validationContext"
              >
                <b-form-group
                  id="example-input-group-3"
                  label="Sector Information"
                  label-for="desc"
                >
                  <b-form-textarea
                    id="desc"
                    name="information"
                    v-model="form.information"
                    placeholder="Enter something..."
                    rows="3"
                    max-rows="6"
                    :state="getValidationState(validationContext)"
                    aria-describedby="input-3-live-feedback"
                  >
                  </b-form-textarea>

                  <b-form-invalid-feedback id="input-3-live-feedback">{{
                    validationContext.errors[0]
                  }}</b-form-invalid-feedback>
                </b-form-group>
              </validation-provider>

              <b-button type="submit" variant="success">Save</b-button>
              <b-button class="ml-2" variant="warning" @click="resetForm()"
                >Reset</b-button
              >
            </b-form>
          </validation-observer>
        </v-card-text>
      </v-card>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";

const VueScrollTo = VueScrollTo;

export default {
  mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [
      { title: "Features"},
      { title: "Sectors",route: "sectors" }
    ]);
  },
  data() {
    return {
      records: [],
      edit: false,
      record_id: "",
      form: {
        title: null,
        information: null,
      },
      search: "",
      headers: [
        {
          text: "ID",
          align: "start",
          sortable: false,
          value: "id",
        },
        { text: "Title", value: "title" },
        { text: "Information", value: "information" },
        { text: "Actions", value: "actions", sortable: false },
      ],
    };
  },

  created() {
    this.fetchSectors();
  },
  methods: {
    getValidationState({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null;
    },
 
    resetForm() {
      this.form = {
        title: null,
        information: null,
      };


      this.$nextTick(() => {
        this.$refs.observer.reset();
      });
    },
    fetchSectors() {
      this.$ApiService
        .get("/Sectors")
        .then((response) => {
          this.records = response.data.data;
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },

    addSector() {
      if (this.edit === false) {
        this.$ApiService
          .post("/Sector", this.form)
          .then((response) => {
            if (response.data.error == 1) {
              this.resetForm();

              this.$refs.table.scrollIntoView();
              this.fetchSectors();
              Vue.$toast.success(`${response.data.result}`);
              
            } else {
              console.log(response.data);
              Vue.$toast.error(`${response.data.result}`);
            }
          })
          .catch((err) => Vue.$toast.error(`${err}`));
      } else {
        //update record
        this.update();
      }
    },

    onSubmit() {
      this.addSector();
    },
    addItem() {
      this.edit = false;
      this.resetForm();
    },
    editItem(item) {
      this.edit = true;
      this.record_id = item.id;
      this.form.title = item.title;
      this.form.information = item.information;
    },
    deleteItem(item) {
      this.$confirm({
        title: "Confirmation",
        message: `Are you sure you want to proceed?`,
        button: {
          no: "No",
          yes: "Yes",
        },
        callback: (confirm) => {
          if (confirm) {
            this.$ApiService
              .delete(`/Sector/${item.id}`)
              .then((response) => {
                if (response.data.error == 1) {
                  this.$refs.table.scrollIntoView();
                  this.fetchSectors();
                  Vue.$toast.success(`${response.data.result}`);
                } else {
                  Vue.$toast.error(`${response.data.result}`);
                }
              })
              .catch((err) => Vue.$toast.error(`${err}`));
          }
        },
      });
    },
    update() {
      this.$ApiService
        .put(`/Sector/${this.record_id}`,this.form)
        .then((response) => {
          if (response.data.error == 1) {
            this.resetForm();
            this.edit = false;
            this.record_id = "";

            this.$refs.table.scrollIntoView();
            this.fetchSectors();
            Vue.$toast.success(`${response.data.result}`);
            
          } else {
            Vue.$toast.error(`${response.data.result}`);
          }
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },
  }, // end of methods
    computed: {
    ...mapState({
      errors: state => state.auth.errors
      
    }),
   
  }
}; //end of class
</script>
